import React, { useEffect, useState } from "react";
import ReactPlayer from "react-player/youtube";
import "./Tutorial.css";

const Playlist2 = () => {
  const [videos, setVideos] = useState([]);
  const [displayVideo, setDisplayVideo] = useState([]);

  useEffect(() => {
    fetch("http://localhost:5000/playlist2")
      .then((res) => res.json())
      .then((data) => {
        setVideos(data);
        setDisplayVideo(data);
      });
  }, []);

  const handleOnChange = (e) => {
    const searchText = e.target.value;
    const filterVideo = videos.filter((video) =>
      video.title.toLowerCase().includes(searchText.toLowerCase())
    );
    setDisplayVideo(filterVideo);
  };

  return (
    <div>
      <div style={{ paddingLeft: "22px" }}>
        <div className="input-group  video-search w-50">
          <input
            style={{ width: "50px" }}
            type="text"
            className="form-control"
            placeholder="Search Tutorial.."
            onChange={handleOnChange}
          />
        </div>
        <div className="playlist">
          {displayVideo.map((video) => (
            <div
              className="video-player-bg mt-4"
              style={{ width: "530px" }}
              key={video.id}
            >
              <div>
                <ReactPlayer
                  className="react-player"
                  url={video.url}
                  width="530px"
                  height="300px"
                  volume
                  controls
                />
              </div>
              <p
                style={{
                  color: "black",
                  fontWeight: "700",
                  textAlign: "start",
                  paddingTop: "10px",
                  paddingLeft: "10px",
                  paddingRight: "10px",
                }}
              >
                {video.title}
              </p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Playlist2;
