import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import React, { useEffect, useState } from "react";
import Swal from "sweetalert2";

const AddSuperAgent = () => {
  const [superagents, setSuperagents] = useState([]);
  const [id, setId] = useState("");
  const [type, setType] = useState("");
  const [name, setName] = useState("");
  const [number, setNumber] = useState("");
  const [facebook, setfacebook] = useState("");
  const [messenger, setMessenger] = useState("");

  useEffect(() => {
    fetch("http://localhost:5000/superagent")
      .then((res) => res.json())
      .then((data) => setSuperagents(data));
  }, [superagents]);

  const handleSubmit = (e) => {
    fetch(`http://localhost:5000/superagent`, {
      method: "POST",
      headers: { "content-type": "application/json" },
      body: JSON.stringify({
        id: id,
        type: type,
        name: name,
        phone: number,
        facebook: facebook,
        messenger: messenger,
      }),
    })
      .then((res) => res.json())
      .then((result) => {});

    e.preventDefault();
    setType("");
    setName("");
    setNumber("");
    setfacebook("");
    setMessenger("");
    setId("");
  };

  const handleDelete = (id) => {
    Swal.fire({
      icon: "warning",
      title: "Are you sure to delete ?",
      showCancelButton: true,
      confirmButtonText: "Yes",
    }).then((result) => {
      if (result.isConfirmed) {
        const url = `http://localhost:5000/superagent/${id}`;
        fetch(url, {
          method: "DELETE",
        })
          .then((res) => res.json())
          .then((data) => {
            if (data.deletedCount) {
              Swal.fire("Deleted!", "", "success");
            }
          });
      }
    });
  };
  return (
    <div>
      <h3 className="text-center pt-3" style={{ color: "#2c9e8f" }}>
        Super Agent
      </h3>
      <TableContainer component={Paper} className="table dashboard-table mt-4">
        <form onSubmit={handleSubmit}>
          <div className="input-group mb-4 p-3">
            <input
              value={id}
              type="number"
              aria-label="id"
              placeholder="ID"
              className="form-control"
              required
              onChange={(e) => setId(e.target.value)}
            />
            <input
              value={type}
              type="text"
              aria-label="Type"
              placeholder="Type"
              className="form-control"
              required
              onChange={(e) => setType(e.target.value)}
            />
            <input
              value={name}
              type="text"
              placeholder="Full Name"
              onChange={(e) => setName(e.target.value)}
              className="form-control"
              required
            />
            <input
              value={number}
              type="number"
              placeholder="Phone number"
              className="form-control"
              onChange={(e) => setNumber(e.target.value)}
            />
            <input
              value={facebook}
              type="text"
              placeholder="Facebook Link"
              className="form-control"
              onChange={(e) => setfacebook(e.target.value)}
            />
            <input
              value={messenger}
              type="text"
              placeholder="Messenger Link"
              className="form-control"
              onChange={(e) => setMessenger(e.target.value)}
            />

            <input type="submit" value="Add New" className="addButton" />
          </div>
        </form>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow style={{ background: "#67f0c2" }}>
              <TableCell className="tableCell">ID</TableCell>
              <TableCell className="tableCell">TYPE</TableCell>
              <TableCell className="tableCell">NAME</TableCell>
              <TableCell className="tableCell">PHONE APP LINK </TableCell>

              <TableCell className="tableCell"> Delete</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {superagents?.map((pd) => (
              <TableRow key={pd._id}>
                <TableCell className="tableCell">{pd.id}</TableCell>
                <TableCell className="tableCell">{pd.type}</TableCell>

                <TableCell className="tableCell">{pd.name}</TableCell>
                <TableCell className="tableCell">
                  {pd.phone}
                  <br /> {pd.facebook}
                  <br /> {pd.messenger}
                </TableCell>
                <TableCell className="tableCell">
                  <div
                    className="deleteButton"
                    onClick={() => handleDelete(pd._id)}
                  >
                    <span>Delete</span>
                  </div>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default AddSuperAgent;
