import AccountCircleOutlinedIcon from "@mui/icons-material/AccountCircleOutlined";
import DashboardIcon from "@mui/icons-material/Dashboard";
import ExitToAppIcon from "@mui/icons-material/ExitToApp";
import PersonOutlineIcon from "@mui/icons-material/PersonOutline";
import VideoLibraryIcon from "@mui/icons-material/VideoLibrary";
import { Link, Outlet } from "react-router-dom";
import useAuth from "../Hook/useAuth";
import "./Dashboard.scss";
import Navbar from "./navbar/Navbar";
const Dashboard = () => {
  const { logOut, user } = useAuth();

  return (
    <div className="row" style={{ backgroundColor: "white" }}>
      <Navbar />
      {user.email && (
        <div className="col-md-2">
          <div className="sidebar">
            <div className="top">
              <span className="logo">LC247-Dashboard</span>
            </div>

            <hr />
            <div className="center">
              <ul>
                <p className="title">MAIN</p>
                <Link to="/admin" style={{ textDecoration: "none" }}>
                  <li>
                    <DashboardIcon className="icon" />
                    <span>Dashboard</span>
                  </li>
                </Link>
                <p className="title">LISTS</p>
                <Link
                  to="/admin/addcustomerservice"
                  style={{ textDecoration: "none" }}
                >
                  <li>
                    <PersonOutlineIcon className="icon" />
                    <span>Customer Service</span>
                  </li>
                </Link>
                <Link to="/admin/addadmin" style={{ textDecoration: "none" }}>
                  <li>
                    <PersonOutlineIcon className="icon" />
                    <span>Admin</span>
                  </li>
                </Link>
                <Link
                  to="/admin/seniorsubadmin"
                  style={{ textDecoration: "none" }}
                >
                  <li>
                    <PersonOutlineIcon className="icon" />
                    <span>Senior Sub Admin</span>
                  </li>
                </Link>
                <Link to="/admin/subadmin" style={{ textDecoration: "none" }}>
                  <li>
                    <PersonOutlineIcon className="icon" />
                    <span>Sub Admin</span>
                  </li>
                </Link>
                <Link
                  to="/admin/addsuperagent"
                  style={{ textDecoration: "none" }}
                >
                  <li>
                    <PersonOutlineIcon className="icon" />
                    <span>Super Agent</span>
                  </li>
                </Link>
                <Link
                  to="/admin/addmasteragent"
                  style={{ textDecoration: "none" }}
                >
                  <li>
                    <PersonOutlineIcon className="icon" />
                    <span>Master Agent</span>
                  </li>
                </Link>
                <Link
                  to="/admin/addcomplain1"
                  style={{ textDecoration: "none" }}
                >
                  <li>
                    <PersonOutlineIcon className="icon" />
                    <span>Complain 1</span>
                  </li>
                </Link>
                <Link
                  to="/admin/addcomplain2"
                  style={{ textDecoration: "none" }}
                >
                  <li>
                    <PersonOutlineIcon className="icon" />
                    <span>Complain 2</span>
                  </li>
                </Link>

                <p className="title">Video Tutorial</p>
                <Link
                  to="/admin/addplaylist1"
                  style={{ textDecoration: "none" }}
                >
                  <li>
                    <VideoLibraryIcon className="icon" />
                    <span>User Tutorial</span>
                  </li>
                </Link>
                <Link
                  to="/admin/addplaylist2"
                  style={{ textDecoration: "none" }}
                >
                  <li>
                    <VideoLibraryIcon className="icon" />
                    <span>Agent Tutorial</span>
                  </li>
                </Link>

                <p className="title">USER</p>

                <li>
                  <AccountCircleOutlinedIcon className="icon" />
                  <span>Admin</span>
                </li>
                <li onClick={() => logOut()}>
                  <ExitToAppIcon className="icon" />
                  <span>Logout</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      )}
      <div className="col-md-10" style={{ paddingRight: "42px" }}>
        <Outlet></Outlet>
      </div>
    </div>
  );
};

export default Dashboard;
