import Paper from "@mui/material/Paper";
import TableContainer from "@mui/material/TableContainer";
import React, { useEffect, useState } from "react";
import ReactPlayer from "react-player/youtube";
import Swal from "sweetalert2";

const Addplaylist2 = () => {
  const [videos, setVideos] = useState([]);
  const [url, setUrl] = useState("");
  const [videoTitle, setVideoTitle] = useState("");

  useEffect(() => {
    fetch("http://localhost:5000/playlist2")
      .then((res) => res.json())
      .then((data) => setVideos(data));
  }, [videos]);

  const handleSubmit = (e) => {
    fetch(`http://localhost:5000/playlist2`, {
      method: "POST",
      headers: { "content-type": "application/json" },
      body: JSON.stringify({
        url: url,
        title: videoTitle,
      }),
    })
      .then((res) => res.json())
      .then((result) => {});

    e.preventDefault();
    setVideoTitle("");
    setUrl("");
  };

  const handleDelete = (id) => {
    Swal.fire({
      icon: "warning",
      title: "Are you sure to delete ?",
      showCancelButton: true,
      confirmButtonText: "Yes",
    }).then((result) => {
      if (result.isConfirmed) {
        const url = `http://localhost:5000/playlist2/${id}`;
        fetch(url, {
          method: "DELETE",
        })
          .then((res) => res.json())
          .then((data) => {
            if (data.deletedCount) {
              Swal.fire("Deleted!", "", "success");
            }
          });
      }
    });
  };
  return (
    <div>
      <h3 className="text-center pt-3" style={{ color: "#2c9e8f" }}>
        Agent Tutorial
      </h3>
      <TableContainer component={Paper} className="table dashboard-table mt-4">
        <form onSubmit={handleSubmit}>
          <div className="input-group mb-4 p-3 col-md-1">
            <input
              value={url}
              type="text"
              aria-label="id"
              placeholder="Video Link"
              className="form-control "
              required
              onChange={(e) => setUrl(e.target.value)}
            />

            <input
              value={videoTitle}
              type="text"
              aria-label="Type"
              placeholder="Video Title"
              className="form-control"
              required
              onChange={(e) => setVideoTitle(e.target.value)}
            />

            <input type="submit" value="Add New" className="addButton" />
          </div>
        </form>
        <div className="Addplaylist_video pt-5">
          {videos?.map((video) => (
            <div
              className="video-player-bg "
              style={{ width: "365px", marginBottom: "15px" }}
              key={video._id}
            >
              <div>
                <ReactPlayer
                  className="react-player"
                  url={video.url}
                  width="350px"
                  height="200px"
                  volume
                  controls
                />
              </div>
              <p
                style={{
                  color: "black",
                  fontWeight: "700",
                  textAlign: "start",
                  paddingTop: "10px",
                  paddingLeft: "10px",
                  paddingRight: "10px",
                }}
              >
                <div
                  className="deleteButton"
                  onClick={() => handleDelete(video?._id)}
                >
                  <span>Delete</span>
                </div>
                {video.title}
              </p>
            </div>
          ))}
        </div>
      </TableContainer>
    </div>
  );
};

export default Addplaylist2;
