import { BrowserRouter, Route, Routes } from "react-router-dom";
import "./App.css";
import AuthProvider from "./Components/Context/AuthProvider";
import AddAdmin from "./Components/Dashboard/AddAdmin/AddAdmin";
import AddComplain1 from "./Components/Dashboard/AddComplain1/AddComplain1";
import AddComplain2 from "./Components/Dashboard/AddComplain2/AddComplain2";
import AddMasterAgent from "./Components/Dashboard/AddMasterAgent/AddMasterAgent";
import Addplaylist1 from "./Components/Dashboard/Addplaylist1/Addplaylist1";
import Addplaylist2 from "./Components/Dashboard/Addplaylist2/Addplaylist2";
import AddSeniorSubAdmin from "./Components/Dashboard/AddSeniorSubAdmin/AddSeniorSubAdmin";
import AddSubadmin from "./Components/Dashboard/AddSubadmin/AddSubadmin";
import AddSuperAgent from "./Components/Dashboard/AddSuperAgent/AddSuperAgent";
import AdminRoute from "./Components/Dashboard/AdminRoute/AdminRoute";
import AddcustomerService from "./Components/Dashboard/CustomerService/AddcustomerService";
import Dashboard from "./Components/Dashboard/Dashboard";
import DashboardHome from "./Components/Dashboard/DashboardHome/DashboardHome";
import Login from "./Components/Login/Login";
import Admin from "./Components/Pages/Admin/Admin";
import SeniorSubadmin from "./Components/Pages/Admin/SeniorSubadmin";
import SubAdmin from "./Components/Pages/Admin/SubAdmin";
import COMPLAIN1 from "./Components/Pages/COMPLAIN1/COMPLAIN1";
import Complain2 from "./Components/Pages/COMPLAIN1/Complain2";
import CustomerService from "./Components/Pages/CustomerService/CustomerService";
import Home from "./Components/Pages/Home/Home";
import MasterAgent from "./Components/Pages/MasterAgent/MasterAgent";
import SuperAgent from "./Components/Pages/SuperAgent/SuperAgent";
import Playlist1 from "./Components/Pages/Tutorial/Playlist1";
import Playlist2 from "./Components/Pages/Tutorial/Playlist2";
import Tutorial from "./Components/Pages/Tutorial/Tutorial";
function App() {
  //  right clike disable
  // document.oncontextmenu = document.body.oncontextmenu = function () {
  //   return false;
  // };
  return (
    <AuthProvider>
      <BrowserRouter>
        <Routes>
          <Route index element={<Home />} />
          <Route path="/" element={<Home />} />
          <Route path="/home" element={<Home />} />
          <Route path="/cs" element={<CustomerService />} />
          <Route path="/ad" element={<Admin />} />
          <Route path="/subadmin" element={<SubAdmin />} />
          <Route path="/seniorsubadmin" element={<SeniorSubadmin />} />
          <Route path="/as" element={<SuperAgent />} />
          <Route path="/mg" element={<MasterAgent />} />
          <Route path="/cp1" element={<COMPLAIN1 />} />
          <Route path="/cp2" element={<Complain2 />} />
          <Route path="/login" element={<Login />} />
          <Route path="tutorial" element={<Tutorial />}>
            <Route path="/tutorial" element={<Playlist1 />} />
            <Route path="/tutorial/playlist1" element={<Playlist1 />} />
            <Route path="/tutorial/playlist2" element={<Playlist2 />} />
          </Route>

          <Route path="admin" element={<Dashboard />}>
            <Route path="/admin" element={<DashboardHome />} />
            <Route
              path="/admin/addcustomerservice"
              element={
                <AdminRoute>
                  <AddcustomerService />
                </AdminRoute>
              }
            />
            <Route
              path="/admin/addadmin"
              element={
                <AdminRoute>
                  <AddAdmin />
                </AdminRoute>
              }
            />
            <Route
              path="/admin/subadmin"
              element={
                <AdminRoute>
                  <AddSubadmin />
                </AdminRoute>
              }
            />
            <Route
              path="/admin/seniorsubadmin"
              element={
                <AdminRoute>
                  <AddSeniorSubAdmin />
                </AdminRoute>
              }
            />
            <Route
              path="/admin/addsuperagent"
              element={
                <AdminRoute>
                  <AddSuperAgent />
                </AdminRoute>
              }
            />
            <Route
              path="/admin/addmasteragent"
              element={
                <AdminRoute>
                  <AddMasterAgent />
                </AdminRoute>
              }
            />
            <Route
              path="/admin/addcomplain2"
              element={
                <AdminRoute>
                  <AddComplain2 />
                </AdminRoute>
              }
            />
            <Route
              path="/admin/addcomplain1"
              element={
                <AdminRoute>
                  <AddComplain1 />
                </AdminRoute>
              }
            />
            <Route
              path="/admin/addplaylist1"
              element={
                <AdminRoute>
                  <Addplaylist1 />
                </AdminRoute>
              }
            />
            <Route
              path="/admin/addplaylist2"
              element={
                <AdminRoute>
                  <Addplaylist2 />
                </AdminRoute>
              }
            />
          </Route>
        </Routes>
      </BrowserRouter>
    </AuthProvider>
  );
}

export default App;
