import React, { useEffect, useState } from "react";
import Footer from "../Shered/Header/Footer";
import Header from "../Shered/Header/Header";
import fb from "./../../../Image/fb.png";
import ms from "./../../../Image/messenger.png";
import ws from "./../../../Image/ws.png";
const Admin = () => {
  const [admins, setAdmins] = useState([]);
  useEffect(() => {
    fetch("http://localhost:5000/adminlist")
      .then((res) => res.json())
      .then((data) => setAdmins(data));
  }, []);

  return (
    <>
      <Header />
      <div className="CustomerService">
        <div style={{ minHeight: "50px" }}>
          <table className="text-data">
            <tbody>
              <tr>
                <th className="header header-text" colSpan="16">
                  LC247 ADMIN LIST
                </th>
              </tr>

              <tr className="custom-tr">
                <th align="center">TYPE</th>
                <th align="center">NAME</th>
                <th align="center">PHONE APP LINK</th>
                <th align="center">PHONE NUMBER</th>
              </tr>
              {admins?.map((pd, index) => (
                <>
                  <tr className="even">
                    <td>
                      <b>{pd.type}</b>
                    </td>
                    <td>
                      <b>{pd.name}</b>
                    </td>
                    <td className="text-center">
                      {pd.facebook && (
                        <a href={pd.facebook}>
                          <img
                            src={fb}
                            alt=""
                            style={{
                              height: "25px",
                              width: "25px",
                              marginRight: "30px",
                            }}
                          />
                        </a>
                      )}

                      {pd.messenger && (
                        <a href={`${pd.messenger}`}>
                          <img
                            src={ms}
                            alt=""
                            style={{
                              height: "25px",
                              width: "25px",
                              marginRight: "30px",
                            }}
                          />
                        </a>
                      )}
                      {pd.phone && (
                        <a href={`https://wa.me/${pd.phone}`}>
                          <img
                            src={ws}
                            alt=""
                            style={{
                              height: "25px",
                              width: "25px",
                            }}
                          />
                        </a>
                      )}
                    </td>
                    <td>
                      <a href={`https://wa.me/${pd.phone}`}>{pd.phone}</a>
                    </td>
                  </tr>
                  <tr>
                    <td colSpan="6" style={{ backgroundColor: "#cccccc" }}>
                      <font size="2">
                        (বি: দ্রা:)এজেন্ট দের সাথে হোয়াটসাপ ও ম্যাছেনজার ব্যাতিত অন্য কোন  মাধ্যমে যোগাযোগ ও  লেনদেন করা যাবে না সুতরাং করলে কোম্পানি তার দায়ভার নিবে না।
                      </font>
                    </td>
                  </tr>
                </>
              ))}
            </tbody>
          </table>
        </div>
      </div>
      <Footer />
    </>
  );
};

export default Admin;
