import ExitToAppIcon from "@mui/icons-material/ExitToApp";
import { Link } from "react-router-dom";
import useAuth from "../../Hook/useAuth";
import "./navbar.scss";



const Navbar = () => {
const {user, logOut}=useAuth();

  return (
    <div className="navbar ">
      <div className="wrapper">
        <div >
         
        </div>
        <div className="items">
       
          <div className="item">
       
          </div>
     
          <div className="item">
           
          </div>
        {user.email ? <div className="item deleteButton" onClick={()=>logOut()}>
                <ExitToAppIcon className="icon me-1" />
              <span>Logout</span>
          </div> :
          <Link to="/login" style={{ textDecoration: "none" }}>
          <div className="item deleteButton">
          <ExitToAppIcon className="icon me-1" />
        <span>Login</span>
         </div>
          </Link>

        }
          
        </div>
      </div>
    </div>
  );
};

export default Navbar;
